import React, { useEffect, useState } from 'react';
import Box from '@mui/material/Box';
import Slider from '@mui/material/Slider';
import { styled } from '@mui/material/styles';
import Bars from './Bars';
import { useGetOffersQuery } from '../redux/services/data';

const SliderComp = ({ scoreCallback }) => {
  const [riskScore, setRiskscore] = useState(5);
  const [name, setName] = useState([]);
  const [numdata, setNumData] = useState([]);

  const { data, isError, isFetching } = useGetOffersQuery();

  // const fetchRisks = async () => {
  //   const response = await fetch('http://localhost:8080/api/books');
  //   const data = await response.json();
  //   setData(data);
  //   console.log(data);
  // };

  useEffect(() => {
    data?.map((val) => {
      setName(
        Object.keys(val).filter(
          (c) => c !== '_id' && c !== 'Risk Score/Tolerence'
        )
      );
    });

    if (data) {
      const currentData = [data[riskScore]];
      setNumData(currentData);
    }
    scoreCallback(riskScore);
  }, [data, riskScore]);

  const handleChange = (e, newValue) => {
    setRiskscore(newValue);
  };

  return (
    <div className="col-span-2 flex items-center">
      <div className="bg-[#230B59] h-5/6 w-full relative flex flex-col items-center justify-center">
        <div className="rounded-lg bg-white p-6 w-[310px] xs:w-[345px] md:w-[448px] space-y-4 absolute -top-10 right-28 h-24">
          <div className="flex flex-col">
            <span className="flex justify-between items-center">
              <p className="font-bold text-lg"> Risk score: {riskScore}</p>
              <p>Portfolio</p>
            </span>
            <Box
              sx={{
                width: 320,
                marginBottom: '2rem',
              }}
            >
              <Box sx={{ m: 2.2 }} />
              <div
                style={{
                  background: 'linear-gradient(to right, #f4b896, #CBD5E1)',
                  borderRadius: '10px',
                  height: '0.5rem',
                  display: 'flex',
                  alignItems: 'center',
                }}
              >
                <PrettoSlider
                  aria-label="pretto slider"
                  defaultValue={5}
                  min={0}
                  max={10}
                  value={riskScore}
                  onChange={handleChange}
                />
              </div>
            </Box>
          </div>
        </div>

        <div className="h-96 w-4/6 text-white grid grid-cols-2">
          <div>
            <span className="flex flex-col gap-4 justify-center">
              {name.map((item, i) => {
                return <h2 key={i}>{item}</h2>;
              })}
            </span>
          </div>
          <div className="flex flex-col justify-center">
            {numdata?.map((item) => {
              return (
                <>
                  {' '}
                  <Bars item={item} />{' '}
                </>
              );
            })}
          </div>
        </div>
      </div>
    </div>
  );
};

export default SliderComp;

const PrettoSlider = styled(Slider)({
  color: 'primary',
  height: 8,
  '& .MuiSlider-track': {
    border: 'none',
    display: 'none',
  },
  '& .MuiSlider-rail': {
    display: 'none',
  },
  '& .MuiSlider-thumb': {
    height: 24,
    width: 24,
    backgroundColor: '#fff',
    border: '2px solid currentColor',
    '&:focus, &:hover, &.Mui-active, &.Mui-focusVisible': {
      boxShadow: 'inherit',
    },
    '&:before': {
      display: 'none',
    },
  },
  '& .MuiSlider-valueLabel': {
    lineHeight: 1.2,
    fontSize: 12,
    background: 'unset',
    padding: 0,
    width: 32,
    height: 32,
    borderRadius: '50% 50% 50% 0',
    backgroundColor: '#52af77',
    transformOrigin: 'bottom left',
    transform: 'translate(50%, -100%) rotate(-45deg) scale(0)',
    '&:before': { display: 'none' },
    '&.MuiSlider-valueLabelOpen': {
      transform: 'translate(50%, -100%) rotate(-45deg) scale(1)',
    },
    '& > *': {
      transform: 'rotate(45deg)',
    },
  },
});
