import { createSlice } from '@reduxjs/toolkit';

const initialState = {
  score: 0,
};

const portSlice = createSlice({
  name: 'portfolio',
  initialState,
  reducers: {
    handleScore: (state, action) => {
      state.score = action.payload;
    },
  },
});

export const { handleScore } = portSlice.actions;

export default portSlice.reducer;
