import React from 'react';

const Bars = ({ item }) => {
  return (
    <div>
      <div
        style={{ width: `${item['Nigerian Stocks'] * 800}px` }}
        className={`text-white border-[1px] h-[39px] flex items-center pl-2`}
      >
        {Math.floor(item['Nigerian Stocks'] * 100)}%
      </div>
      <div
        style={{ width: `${item['Foriegn Stocks'] * 800}px` }}
        className={`text-white border-[1px] h-[39px] bg-[#4840BB] flex items-center pl-2`}
      >
        {Math.floor(item['Foriegn Stocks'] * 100)}%
      </div>
      <div
        style={{ width: `${item['Tech Stocks'] * 800}px` }}
        className={`text-white border-[1px] h-[39px] bg-[#4D5898] flex items-center pl-2`}
      >
        {Math.floor(item['Tech Stocks'] * 100)}%
      </div>
      <div
        style={{ width: `${item['Emerging Stocks'] * 800}px` }}
        className={`text-white border-[1px] h-[39px] bg-[#708BC9] flex items-center pl-2`}
      >
        {Math.floor(item['Emerging Stocks'] * 100)}%
      </div>
      <div
        style={{ width: `${item['Nigerian Bonds'] * 800}px` }}
        className={`text-white border-[1px] h-[39px] bg-[#BDC6F5] flex items-center pl-2`}
      >
        {Math.floor(item['Nigerian Stocks'] * 100)}%
      </div>
      <div
        style={{ width: `${item['Foriegn Bonds'] * 800}px` }}
        className={`text-white border-[1px] h-[39px] bg-[#AD6035] flex items-center pl-2`}
      >
        {Math.floor(item['Foriegn Bonds'] * 100)}%
      </div>
      <div
        style={{ width: `${item['Commodities'] * 800}px` }}
        className={`text-white border-[1px] h-[39px] bg-[#DF8C5E] flex items-center pl-2`}
      >
        {Math.floor(item['Commodities'] * 100)}%
      </div>
      <div
        style={{ width: `${item['Real Estate'] * 800}px` }}
        className={`text-white border-[1px] h-[39px] bg-[#FED0B9] flex items-center pl-2`}
      >
        {Math.floor(item['Real Estate'] * 100)}%
      </div>
      <div
        style={{ width: `${item['T-Bills'] * 800}px` }}
        className={`text-white border-[1px] h-[39px] bg-green-500 flex items-center pl-2`}
      >
        {Math.floor(item['T-Bills'] * 100)}%
      </div>
      <div
        style={{ width: `${item['Alternative'] * 800}px` }}
        className={`text-white border-[1px] h-[39px] flex items-center pl-2`}
      >
        {Math.floor(item['Alternative'] * 100)}%
      </div>
    </div>
  );
};

export default Bars;
