import React from 'react';
import logo from '../assets/Logo.svg';
import { ChevronDownIcon } from '@heroicons/react/24/outline';

const Header = () => {
  return (
    <header className="w-full flex flex-col justify-center items-center lg:px-16 py-4 px-0">
      <div className="bg-gradient-to-r from-[#5fa698] via-[#4840bb] to-[#230b59] w-full h-14 rounded px-5 py-4 filter drop-shadow-lg flex justify-center items-center ">
        <h2 className="text-yellow-50 font-bold">
          Now, earn 3.30% with the Wealthfront Cash Account.{' '}
          <span className="underline cursor-pointer"> Get started</span>
        </h2>
      </div>
      <div className="flex justify-between w-full items-center mt-7">
        <div>
          <img src={logo} alt="" />
        </div>
        <div className="flex text-[#230B59] text-lg space-x-12">
          <p className="cursor-pointer flex items-center gap-2">
            Invest{' '}
            <span>
              <ChevronDownIcon className="h-4 w-4" />
            </span>
          </p>
          <p className="cursor-pointer">Save</p>
          <p className="cursor-pointer">Borrow</p>
          <p className="cursor-pointer flex items-center gap-2">
            Learn{' '}
            <span>
              <ChevronDownIcon className="h-4 w-4" />
            </span>
          </p>
        </div>
        <div className="space-x-7">
          <button className="bg-transparent hover:bg-[#230B59] text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded">
            Log in
          </button>
          <button className="bg-[#230B59] hover:bg-transparent hover:text-[#230B59] text-white font-bold py-2 px-4 border border-blue-700 rounded">
            Get started
          </button>
        </div>
      </div>
    </header>
  );
};

export default Header;
